<template>
 <div class="lector">
     <Mheader></Mheader>
    <div class="warpper-box">
        <div class="mw content">
            <h2></h2>
            <p></p>
            <p></p>
            <ul>
                <li></li>
                <li></li>
                <li>3</li>
                <li>JAVAPythongoUI</li>
                <ol></ol>
            </ul>
            <button @click="Add"></button>
        </div>
   </div>
 </div>
</template>

<script type="text/javascript">
import Mheader from '@/components/Mheader'

export default {
   data() {
     return {

      }
  },
  components:{
      Mheader
  },
  methods:{
      Add(){
          var _this = this
            _this.$router.push({
                path:'/about/apply'
            })
      }
  }
 }
</script>

<style scoped lang="less">
.content{
  background: #fff;
  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.1);
  padding: 20px 30px 60px 30px;
  text-align: left;
  ul{
      margin-left: 30px;
      li{
          list-style-type : disc;
          line-height: 30px;
          font-size: 12px;
          color: #666;
      }
  }
  }
.mw{
    width: 1000px;
    margin: 150px auto;
}
h2{
    font-weight: normal;
    font-size: 18px;
    margin-bottom: 10px;
    padding-bottom: 14px;
    border-bottom: 1px solid #BBBBBB;
}
button{
    border: 1px solid #ff7413;
    width: 100px;
    margin-left: 20px;
    height:34px;
    border-radius: 4px;
    font-size: 12px;
    color: #fff;
    cursor: pointer;
    background: #fff;
    margin-top: 20px;
    background: #ff7413;
}
p{
    line-height: 30px;
}
</style>



