// mockjs
const Mock = require('mockjs');
//  mock.Random 
const Random = Mock.Random;
// mock
const produceNewsData = function() {
    let articles = [];
    for (let i = 0; i < 6; i++) {
        let newArticleObject = {
            class_name: Random.cword(4), // 
            course_name: Random.csentence(5, 30), //  Random.csentence( min, max )
            course_img: Random.dataImage('200x170', 'mock'), // Random.dataImage( size, text )  Base64 
            adv_img: Random.dataImage('200x470', 'mock'), // Random.dataImage( size, text )  Base64 
            course_price: Random.natural(60, 100), // Random.course_price() 
            course_number: Random.natural(60, 100), // Random.course_number() 
        }
        articles.push(newArticleObject)
    }
    return {
        articles: articles
    }
}

// Mock.mock( url, post/get , )
Mock.mock('/news/index', 'post', produceNewsData);


